* {
  box-sizing: border-box;
}

.movie {
  margin: 0 auto;
  padding: 0;
  justify-content: center;
}

.movieCard {
  position: relative;
  margin: 20px 10px;
  max-width: 100%;
  /*padding: 8px;*/
}
.movie-poster {
  position: relative;
  width: 250px;
  height: 350px;
}
.movieimg {
  width: 250px;
  height: 350px;
  border-radius: 10px;
}
.movie-poster:hover + .mv-inf {
  visibility: visible;
}

.mv-inf:hover {
  visibility: visible;
}

.mv-inf {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  padding: 8px;
  width: 250px;
  align-self: end;
  background-color: white;
  border-radius: 0 0 8px 8px;
  /*margin: 8px;*/
  visibility: hidden;
  transition: visibility 500ms;
}

.mv-inf-top {
  text-align: left;
  font-weight: bolder;
}

.mv-inf-btm {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}
.mv-inf-overview {
  display: flex;
  flex-wrap: wrap;
  text-align: left;
}

.mv-inf-vote-avg {
  display: flex;
  flex-direction: column;
  /*justify-content: center;*/
  border-radius: 8px;
  background-color: red;
  /*padding-left:8px;*/
  /*padding-right: 8px;*/
  padding: 4px;
  color: white;
  /*height: 22px;*/
  /*align-self: center;*/
  text-align: center;
}

.mv-inf-vote-avg .star {
  margin: 0 auto;
  color: yellow;
}
@media only screen and (max-width: 600px) {
  .movie-poster {
    position: relative;
    width: 125px;
    height: 175px;
  }
  .movieimg {
    width: 125px;
    height: 175px;
    border-radius: 10px;
  }

  .movieCard {
    margin: 0;
  }

  .mv-inf {
    width: 125px;
    font-size: 12px;
  }

  .mv-inf-btm {
    font-size: 10px;
  }
}

.container {
  height: 100%;
  display: flexbox;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fill, minmax(auto, 260px));
  grid-gap: 0.5%;
  padding: 3%;
  width: 80%;
  padding-top: 1%;
  justify-content: center;
}

.page-linkh {
  border-radius: 4px;
  background-color: rgba(255, 125, 125, 0.5);
}

.pagination {
  display: flex;
  justify-content: center;
}
.pagination ul {
  flex-wrap: unset;
  margin: 0 auto;
  margin-top: 2%;
}

.left {
  font-size: 12px;
  color: black;
}
@media screen and (max-width: 600px) {
  .movies {
    grid-template-columns: repeat(2, minmax(auto, 150px));
    max-width: 100%;
    margin-top: 4%;
  }
}

:root {
  --text-color: #f2dcb3;
  --background-color: #1b1159;
  --accent-color: #87cbff;
}

body {
  margin: 0;
  font-family: "Noto Sans KR";
}

.nav a {
  text-decoration: none;
  font-family: "Sansita Swashed", cursive;
  font-weight: 500;
  color: #f2dcb3;
  color: var(--text-color);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1b1159;
  background-color: var(--background-color);
  padding: 8px 12px;
  color: #f2dcb3;
  color: var(--text-color);
}

.navbar__logo {
  font-size: 24px;
  display: flex;
  color: #f2dcb3;
  color: var(--text-color);
}
.navbar__logo a {
  font-size: 24px;
  margin-left: 5px;
  margin-top: auto;
  margin-bottom: auto;
}

.navbar__logo img {
  width: 40px;
  height: 40px;
}

.navbar__menuhidden {
  display: flex;
  list-style: none;
  padding-left: 0;
}

.navbar__menuhidden li {
  padding: 8px 12px;
}
.navbar__menuhidden li:hover {
  background-color: #87cbff;
  background-color: var(--accent-color);
  border-radius: 4px;
}

.navbar__iconshidden {
  list-style: none;
  color: #f2f2f2;
  display: flex;
  padding-left: 0;
}

.navbar__iconshidden li {
  padding: 8px 12px;
}

.navbar__iconshidden li img {
  height: 70px;
  margin: -30px 0;
}

.navbar__iconsshow li img {
  height: 70px;
  margin: -30px 0;
}

.navbar__toogleBtn {
  display: none;
  position: absolute;
  right: 32px;
  top: 20.5px;
  font-size: 24px;
  color: #87cbff;
  color: var(--accent-color);
}

@media screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 24px;
  }

  .navbar__menushow {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .navbar__menu li {
    width: 100%;
    text-align: center;
  }

  .navbar__iconsshow {
    display: flex;
    justify-content: center;
    color: #f2f2f2;
    width: 100%;
    margin-top: 5%;
  }

  .navbar__toogleBtn {
    display: flex;
    position: absolute;
    right: 32px;
    top: 20.5px;
  }
  .navbar__toogleBtn element.style {
    position: absolute;
  }

  element .navbar__toogleBtn {
    position: absolute;
  }

  .navbar__iconsshow li img {
    height: 60px;
    margin: -20px 7px;
  }

  .navbar__menuhidden,
  .navbar__iconshidden {
    display: none;
  }
}

